const initialError = {
  name: '',
  username: '',
  email: '',
  password: '',
  message: '',
}

export default {
  name: 'RegisterPage',
  layout: 'auth',
    middleware: 'guest',
    data() {
    return {
      form: {
        name: '',
        username: '',
        email: '',
        password: '',
      },
      errors: initialError,
      loading: false,
    }
  },
  head: {
    title: 'Daftar'
  },
  methods: {
    async submit(data) {
      this.loading = true
      this.errors = initialError
      try {
        await this.$store.dispatch('auth/register', data)
        await this.$store.dispatch('notification/listen')
        this.redirect()
      } catch (e) {
        data = this.$errorResponse(e)
        this.errors = { ...this.errors, ...data.errors, message: data.message }
      }
      this.loading = false
    },
    redirect() {
      if (this.$route.query.next) {
        return this.$router.push(this.$route.query.next)
      }
      return this.$router.push('/home')
    },
  },
}
